import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {concatMap} from 'rxjs/internal/operators/concatMap';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {GroupProductPagingViewModel, GroupProductViewModel} from 'src/app/core/models/common/respond/ProductRespondBase';
import {ProductShowHomePageRespond} from 'src/app/core/models/product/respond/ProductShowHomePageRespond';
import { NotificationService } from 'src/app/core/notification/notifications/customer/notification.service';
import { AuthService } from 'src/app/core/services/auth/auth.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SortProductService} from 'src/app/core/services/comon-services/sort-product/sort-product.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import { CartService } from 'src/app/core/services/customer/cart/cart.service';
import {ProductService} from 'src/app/core/services/customer/product/product.service';

@Component({
	selector: 'app-customer-group-product',
	templateUrl: './customer-group-product.component.html',
	styleUrls: ['./customer-group-product.component.less'],
	providers: [DestroyService]
})
export class CustomerGroupProductComponent implements OnInit {
	isLoading = true; //skelton
	isNotHaveData = false;
	groupProductId: number;
	addCartLoading: boolean;
	groupProducts: GroupProductPagingViewModel;
	pageResult: PagedResult<ProductShowHomePageRespond>;
	constructor(
		private productService: ProductService,
		private route: ActivatedRoute,
		private sortProductService: SortProductService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService,
		private destroy$: DestroyService,
		private authService: AuthService,
		private cartService: CartService,
		private notificationService: NotificationService,
		private router: Router,
	) {}

	ngOnInit(): void {
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('groupProductId')) {
			this.groupProductId = +this.route.snapshot.paramMap.get('groupProductId');
			console.log(this.groupProductId);
			if (this.groupProductId) {
				this.getGroupProduct();
			}
		}
	}
	addGroupProductToCart(groupProductId:number, quantityNumber: number) {
		this.addCartLoading = true;
		if (this.authService.isAuthenticated('customer-token')) {
			this.cartService
				.addToCart(groupProductId, quantityNumber, 2)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(result) => {
						if (result.isSuccessed) {
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.addCartLoading = false;
					},
					(error) => {
						this.addCartLoading = false;
						this.appErrorHandler.handleErrorWithNoti(error);
					},
					() => {
						this.addCartLoading = false;
					}
				);
		} else {
			this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
		}
	}
	getGroupProduct() {
		console.log('this.groupProductId ');
		this.isLoading = true; //skelton
		this.sortProductService
			.getQuery(this.route)
			.pipe(
				takeUntil(this.destroy$),
				concatMap((value) => {
					this.isLoading = true;
					return this.productService.getGroupProduct({
						pageIndex: value.page == undefined ? 1 : value.page,
						pageSize: 10,
						sortby: value.sortBy,
						order: value.order,
						id:this.groupProductId,
					});
				})
			)
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						this.groupProducts=result.resultObj;
						this.pageResult = result.resultObj.productShowHomePageResponds;
						console.log(this.pageResult);

						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					} else {
						this.isNotHaveData = true;
					}
					this.isLoading = false;
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				},
				() => {
					this.isLoading = false;
				}
			);
	}
}
