import { CustomerGroupProductComponent } from './customer-group-product/customer-group-product.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

const routes: Routes = [
	{
		path: ':groupProductId',
		component: CustomerGroupProductComponent,
		data: {
			title: 'Customer-app.body.group-product.title',
			breadcrumb: 'Group Product'
		}
	}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class CustomerGroupProductRoutingModule {}
